import ReactEngine from '../react/ReactEngine';
import * as CRAFiles from './../CRAFiles';

class ReactCodeEngine {
  reactEngine: ReactEngine = new ReactEngine();

  constructor() {}
  meta: any = {
    name: 'React',
    displayName: 'React',
    icon:
      'https://pre-cloud.builderx.io/assets/plugins/component/icons/React.svg',
  };
  execute: any = (fileJSON: any, config: any) => {
    return this.reactEngine.getFileCode(fileJSON, config);
  };

  getFiles: any = () => {
    return CRAFiles;
  };

  getFilesToWrite: any = () => {
    const filesMap = new Map();

    filesMap.set('/package.json', {
      content: JSON.stringify(CRAFiles.packageJSON, null, 2),
    });
    filesMap.set('/.gitignore', { content: CRAFiles.gitIgnoreContent });
    filesMap.set('/src/serviceWorker.js', {
      content: CRAFiles.serviceWorkerJSContent,
    });
    filesMap.set('/src/index.js', { content: CRAFiles.indexJS });
    filesMap.set('/src/index.css', { content: CRAFiles.indexCSS });
    filesMap.set('/src/App.test.js', { content: CRAFiles.appTestJS });
    filesMap.set('/public/index.html', { content: CRAFiles.indexHTML });
    filesMap.set(
      '/public/manifest.json',
      JSON.stringify(CRAFiles.publicManifest, null, 2)
    );
    filesMap.set('/public/favicon.ico', {
      content: CRAFiles.favicon,
      config: {
        base64: true,
      },
    });
    filesMap.set('/config-overrides.js', { content: CRAFiles.projectConfig });
    filesMap.set('/src/icons.js', { content: CRAFiles.iconsJS });
    filesMap.set('/README.md', { content: CRAFiles.projectReadme });

    return filesMap;
  };
  writeFiles: any = (zip: any) => {
    zip.file('/package.json', JSON.stringify(CRAFiles.packageJSON, null, 2));
    zip.file('/.gitignore', CRAFiles.gitIgnoreContent);
    zip.file('/src/serviceWorker.js', CRAFiles.serviceWorkerJSContent);
    zip.file('/src/index.js', CRAFiles.indexJS);
    zip.file('/src/index.css', CRAFiles.indexCSS);
    zip.file('/src/App.test.js', CRAFiles.appTestJS);
    zip.file('/public/index.html', CRAFiles.indexHTML);
    zip.file(
      '/public/manifest.json',
      JSON.stringify(CRAFiles.publicManifest, null, 2)
    );
    zip.file('/public/favicon.ico', CRAFiles.favicon, { base64: true });
    zip.file('/config-overrides.js', CRAFiles.projectConfig);
    zip.file('/src/icons.js', CRAFiles.iconsJS);
    zip.file('/README.md', CRAFiles.projectReadme);
  };
}

export default class ReactCodeGen {
  name = 'ReactCodeGen';
  session: any;
  init(session: any) {
    this.session = session;
  }
  activate() {
    const codeEngine = new ReactCodeEngine();
    this.session.addCodeEngine(codeEngine);
  }

  deactivate() {}

  logSession() {
    this.session.executeFunction('logSession');
  }
}
